:root {
  --black: #111;
  --dark: #1e1823;
  --orange: #fd9222;
  --light-orange: #fcf1e0;
  --white: #fafafa;
}

body {
  font-family: montserrat;
}

@media screen and (min-width: 375px) and (max-width: 767px) {
  .header {
    background-color: var(--white);
    width: 100%;
    height: 68px;
    padding: 20px 0;
  }

  .header-heading {
    display: none;
  }

  .header-logo {
    justify-content: space-between;
    align-items: center;
    height: 28px;
    margin: 0 auto;
    padding: 0 20px;
    display: flex;
  }

  .header-logo-list {
    text-transform: uppercase;
    gap: 4px;
    align-items: inherit;
    width: 171px;
    height: 21px;
    padding: 0;
    list-style: none;
    display: inline-flex;
  }

  .header-logo-svg {
    fill: #0000;
    width: 24px;
    height: 24px;
  }

  .header-media-svg {
    fill: #0000;
    stroke: var(--white);
    width: 24px;
    height: 24px;
  }

  .header-simply {
    color: var(--black);
    letter-spacing: -.42px;
    font-family: Montserrat;
    font-size: 14px;
    font-style: italic;
    font-weight: 700;
    line-height: 18px;
    text-decoration: none;
  }

  .header-chocolate {
    color: var(--black);
    letter-spacing: -.42px;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 18px;
  }

  .header-nav, .header-media {
    display: none;
  }

  .header-hamburger-button {
    stroke: var(--black);
    background-color: #0000;
    border: none;
  }

  .header-hamburger-menu {
    z-index: 5;
    background-color: #0000;
    justify-content: flex-end;
    width: 100%;
    height: 100%;
    transition: all .5s;
    display: flex;
    position: fixed;
    right: -100%;
  }

  .header-hamburger-menu.active {
    right: 0;
  }

  .header-hamburger-menu-container {
    background-color: var(--dark);
    flex-direction: column;
    justify-content: space-between;
    width: 220px;
    height: 100%;
    display: flex;
  }

  .hamburger-close-button {
    stroke: var(--white);
    background-color: #0000;
    border: none;
    width: 28px;
    height: 28px;
  }

  .hamburger-close {
    stroke: var(--white);
    background-color: #0000;
    border: none;
    justify-content: flex-end;
    padding: 20px;
    display: flex;
  }

  .hamburger-close-button:hover {
    stroke: var(--orange);
  }

  .hamburger-close-icon {
    width: 28px;
    height: 28px;
  }

  .hamburger-menu-list {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 24px;
    width: 100%;
    height: 176px;
    list-style: none;
    display: flex;
  }

  .hamburger-link {
    color: var(--white);
    letter-spacing: -.28px;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 16px;
    text-decoration: none;
  }

  .hamburger-link:hover {
    color: var(--orange);
  }

  .hamburger-media {
    justify-content: flex-start;
    gap: 18px;
    padding: 20px;
    list-style: none;
    display: flex;
  }

  .hamburger-media-list {
    list-style: none;
  }

  .hamburger-media-svg-insta {
    stroke: var(--white);
    fill: #0000;
    width: 22px;
    height: 22px;
  }

  .hamburger-media-svg-insta:hover {
    stroke: var(--orange);
  }

  .hamburger-media-svg-twit {
    stroke: var(--white);
    fill: #0000;
    width: 20px;
    height: 20px;
  }

  .hamburger-media-svg-twit:hover {
    stroke: var(--orange);
  }
}

