    list-style-type: none;
    display: flex;
  }

  .unique_components_part {
    background: var(--white);
    border: 1px solid #fd9222;
    border-radius: 15px;
    width: 336px;
    height: 270px;
  }

  .unique_components_part_choco {
    background-color: var(--orange);
    border-radius: 15px;
    width: 336px;
    height: 270px;
  }

  .unique_components_part_nuts {
    background-color: var(--dark);
    border-radius: 15px;
    width: 336px;
    height: 270px;
  }

  .unique_components_pictures_desktop {
    display: none;
  }

  .unique_components_pictures_mobile {
    margin: 28px 56px;
  }

  .unique_components_li {
    padding-top: 0;
    padding-bottom: 0;
    text-decoration: none;
    display: block;
  }

  .unique_components_wrapper {
    position: relative;
    overflow: hidden;
  }

  .text_up_style {
    color: var(--black);
    background-color: var(--white);
    padding: 20px;
    transition: transform .25s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    inset: 0;
    transform: translateY(100%);
  }

  .unique_components_li:focus .text_up_style, .unique_components_li:hover .text_up_style {
    transform: translateY(0%);
  }

  .unique_components_li:hover .unique_components_part {
    border-color: #0000;
  }

  .unique_components_buttons {
    gap: 6px;
    padding-top: 8px;
    padding-bottom: 14px;
    display: flex;
  }

  .unique_button_orange {
    background: var(--orange);
    border-radius: 35px;
    justify-content: center;
    align-items: center;
    gap: 4px;
    padding: 6px 16px;
    display: flex;
  }

  .unique_button_white {
    background: var(--white);
    border: 1px solid #fd9222;
    border-radius: 35px;
    justify-content: center;
    align-items: center;
    gap: 4px;
    padding: 6px 16px;
    display: flex;
  }

  .button_orange_text {
    color: var(--white);
    font-size: 10px;
    line-height: 12px;
  }

  .button_white_text {
    color: var(--black);
    font-size: 10px;
    line-height: 12px;
  }

  .text_up_style_header {
    letter-spacing: -.54px;
    text-transform: uppercase;
    font-size: 18px;
    font-weight: 600;
    line-height: normal;
  }

  .unique_components_textup {
    text-overflow: ellipsis;
    letter-spacing: -.28px;
    width: 280px;
    line-height: 18px;
    overflow: hidden;
  }
